import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange, onclickEdit, onClickDone }) => {
  useEffect(() => {}, []);

  return (
    <tr>
      <th scope="row">{item.id}</th>
      <td>
        {item.isEditable ? (
          <Input
            type="number"
            value={item.num}
            onChange={event => OnItemChange({ ...item, num: event.target.value })}
          />
        ) : (
          item.num
        )}
      </td>
      <td>
        {item.isEditable ? (
          <Input
            type="number"
            value={item.weigth}
            onChange={event => onItemChange({ ...item, weight: event.target.value })}
          />
        ) : (
          item.weigth
        )}
      </td>
      <td>
        <Button
          color={!item.isEditable ? 'primary' : 'danger'}
          onClick={
            !isEditable
              ? onclickEdit({ ...item, isEditable: true })
              : onClickDone({ ...item, weight: false })
          }
        >
          {!item.isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
